<template>
  <div id="app">
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div> -->
    <router-view class="content"/>
    <div class="player">
      <aplayer
        :music="songList[0]"
        :list="songList"
        listFolded
        theme="#fe9a3b"
        showLrc
        @play="echionCount()"
      />
      <a href="#" class="info-btn" @click.prevent="songURL()"><i class="fas fa-info-circle"></i></a>
    </div>
    <Menu></Menu>
  </div>
</template>

<script>
import Aplayer from 'vue-aplayer';
import firebase from 'firebase';
import $ from 'jquery';
import Menu from './components/Menu.vue'

export default {
  name: 'App',
  data() {
    return {
      songList: [
        {
          title: '我還年輕',
          artist: '子陽',
          src: 'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/music%2F%E6%88%91%E9%82%84%E5%B9%B4%E8%BC%95.mp3?alt=media&token=21d2d37e-012c-4f37-afcb-083024546670',
          pic: 'htt ps://moeplayer.b0.upaiyun.com/aplayer/secretbase.jpg',
          lrc: '[00:00.00]/partner'
        },
        {
          title: '有一種悲傷',
          artist: 'Silent Siren',
          src: 'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/music%2F%E6%9C%89%E4%B8%80%E7%A8%AE%E6%82%B2%E5%82%B7(%E7%90%AC).mp3?alt=media&token=8a7566bd-197c-4570-8659-a57934dd6309',
          pic: 'htt ps://moeplayer.b0.upaiyun.com/aplayer/secretbase.jpg',
          lrc: `545fs4df45sf45 5d4fswe74re 45f4we 4sdf w47er wf4s`
        }
      ],
      user: null,
    }
  },
  components: {
    Aplayer,
    Menu
  },
  methods: {
    loginOrNot() {
      const vm = this;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.user = user;
        } else {
          vm.user = 'not-log-in';
        }
      });
    },
    songURL() {
      // 歌曲詳細頁面
      const vm = this;
      var str = $('.aplayer-lrc-current').html()
      vm.$router.push(str.replace(/\s+/g,""));
    },
    echionCount() {
      // 瀏覽次數調整
      console.log('瀏覽次數調整')
    }
  },
  created() {
    const vm = this;
    vm.loginOrNot();
    vm.$bus.$on('audio:list',(list) => {
      vm.songList = list;
      setTimeout(() => {
        document.querySelector('.player').getElementsByTagName('audio')[0].play();
      }, 0);      
    })
  },
}
</script>

